.banner {
  position: fixed;
  width: 100%;
  z-index: 100; }

.nav-primary {
  position: absolute;
  top: 43px;
  left: 1.7857142857%;
  .fa-tree {
    position: relative;
    top: 2px;
    font-size: 22px;
    color: #C8D9BF;
    margin-right: 1rem; }

  .nav-bunch {
    display: inline-block;
    padding: 13px 20px 11px 16px;
    background-color: #fbfaf6;
    border-radius: 25px;
    text-align: left;
    a {
      @include fontmake($size: 1, $margin: 0, $family: "Raleway", $weight: 400);
      margin-right: 1rem;
      &:last-of-type {
        margin-right: 0; }
      &:hover {
        text-decoration: none;
        @include textback($pull: 6px, $sides: 0px, $color: #ECDBBC); } } } }

.about-pulldown {
  background-color: $beige;
  padding: 2rem 0;
  display: none;
  .about-pulldown-hero h1 {
    color: $purple;
    @include fontmake($size: 1.5, $margin: 2, $family: "Montserrat", $weight: 700); }
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 400px;
    top: 0px;
    background-color: $beige;
    z-index: -1; }
  .about-pulldown-third {
    height: 200px;
    text-align: center;
    padding-top: 4rem;
    a {
      @include fontmake($family: "Aleo", $size: 2, $margin: 0, $weight: 300);
      color: #ECDBBC;
      @include textback($pull: 13px, $color: $purple);
      &:hover {
        color: $red; } } }
  .about-pulldown-mission {
    background-image: url('../../assets/images/nav/mission.png'); }
  .about-pulldown-masthead {
    background-image: url('../../assets/images/nav/masthead.png'); }
  .about-pulldown-contact {
    background-image: url('../../assets/images/nav/contact.png'); } }

.about-pulldown-active {
  display: block; }

.nav-brand {
  height: 175px;
  .ftr-title {
    background-image: url('../../assets/images/ftr_title_black.png');
    background-size: 300px auto;
    background-repeat: no-repeat;
    width: 300px;
    height: 29px;
    margin: 0 auto;
    text-indent: 200%;
    white-space: nowrap;
    overflow: hidden;
    transition: 0.3s opacity ease, 0.5s margin ease; }
  .ftr-logo {
    background-image: url('../../assets/images/ftr_logo.png');
    background-size: 100px 100px;
    background-repeat: no-repeat;
    width: 100px;
    height: 100px;
    margin: 1rem auto;
    border-radius: 50%;
    position: relative;
    transition: 0.3s all ease-in-out;
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    box-shadow: 0 0 0 2px $green, 0 0 0 10px $beige;
    &:hover {
      box-shadow: 0 0 0 12px $green; } } }

.nav-brand.nav-scrolled {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  .ftr-title {
    opacity: 0;
    margin: -180px auto 0 auto; }
  .ftr-logo {
    transform: scale(0.7); } }

.nav-social {
  margin-top: 56px;
  text-align: right;

  a {
    display: inline-block;
    width: 28px;
    height: 28px;
    background-color: #ECDBBC;
    border-radius: 50%;
    text-align: center;

    &:hover {
      text-decoration: none; }

    i {
      font-size: 18px;
      position: relative;
      top: 5px; } } }



