.issue-past {
  width: 100%;

  .issue-image {
    // position: absolute
    img {
      max-width: 100%;
 } }      // height: 500px

  .issue-text {
    padding-top: 3.5rem;
    h3 {
      @include fontmake($family: "Raleway", $size: 1.2, $margin: 1, $line: 1.2, $weight: 700); }
    p {
      @include fontmake($family: "Raleway", $size: 0.8);
      &:first-of-type {
        @include fontmake($family: "Raleway", $size: 1); } } } }

.issue-primary-blue {
  background-color: $cover-blue; }
.issue-primary-green {
  background-color: $cover-green; }
.issue-primary-violet {
  background-color: $cover-violet; }
.issue-primary-white {
  background-color: $cover-white; }
.issue-primary-aquamarine {
  background-color: $cover-aquamarine; }
.issue-primary-red {
  background-color: $cover-red; }
.issue-primary-brown {
  background-color: $cover-brown; }
.issue-primary-grey {
  background-color: $cover-grey; }
.issue-primary-yellow {
  background-color: $cover-yellow; }
.issue-primary-emerald {
  background-color: $cover-emerald; }
.issue-primary-mauve {
  background-color: $cover-mauve; }
.issue-primary-ochre {
  background-color: $cover-ochre; }

.issue-secondary-dark {
  color: black;

  h3 {
    color: black; }

  a {
    color: $purple;
    &:hover {
      color: $purple; } } }

.issue-secondary-light {
  color: white;

  h3 {
    color: white; }

  a {
    color: $beige;
    &:hover {
      color: $green; } } }


// footer
footer.footer-main {
  padding: 8rem 0 8rem; }

// tagline
.tagline {
  h1 {
    @include fontmake($family: "Aleo", $size: 2, $margin: 0, $line: 2.4, $weight: 700);
    color: $base;
    text-align: left;
    text-transform: none; } }

// newsletter
.newsletter {

  #mc_embed_signup_scroll {
    text-align: center;
    position: relative;

    h2 {
      margin-bottom: 1rem;
      text-transform: none; }

    .mc-field-group {
      position: relative; }

    label {
      @include fontmake($family: "Raleway", $size: 1.8, $weight: 400); }

    input#mce-EMAIL {
      width: 75%;
      @include fontmake($size: 1, $family: "Raleway", $weight: 400, $margin: 0);
      color: $purple;
      padding: 1rem 1.4rem;
      margin: 1rem 0;
      border: 0;
      outline: 0;
      border-radius: 28px;
      box-shadow: 0 0 0 2px $green, 0 0 0 10px $beige;
      transition: 0.3s all ease;
      &:active {
        border: 0;
        outline: 0; }
      &:focus {
        box-shadow: 0 0 0 12px $green; } }

    #mc-embedded-subscribe {
      width: 60px;
      height: 60px;
      position: absolute;
      top: 14px;
      right: 12%;
      padding: 0;
      background: white;
      @include fontmake($family: "Raleway", $size: 0.8, $weight: 400, $margin: 0);
      border: 2px solid $green;
      border-radius: 30px;
      transition: 0.3s all ease;
      &:hover {
        cursor: pointer;
        border: 2px solid $beige; } }

    .mce_inline_error {
      background-color: transparent;
      color: $red; } } }

// past issues
.past-issues {
  img {
    max-width: 100%; } }


.footer-image {
  width: 100%;
  height: 600px;
  background-image: url('../../assets/images/erika_rier_arbor_home.jpg');
  background-size: 2000px auto;
  background-position: 50% 0;

  span {
    position: relative;
    display: block;
    text-align: center;
    @include fontmake($family: "Raleway", $size: 0.8, $weight: 400, $margin: 0);
    top: -20px; } }
